.signUpPage {
   position: absolute;
   height: 60rem;
   width: min(40rem, 100%);
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   border-radius: 2rem;
   display: flex;
   justify-content: center;
   align-items: center;
   box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.158);
}
